export function createStandardPrimitives(accessorI, materialI) {
  return [{
    mode: 4,
    attributes: {
      POSITION: accessorI + 1,
      NORMAL: accessorI + 2,
      TEXCOORD_0: accessorI + 3,
    },
    material: materialI,
    indices: accessorI,
  }];
}


